import {
  ShellBar,
  ThemeProvider,
  Avatar,
  Button,
  Popover,
  StandardListItem,
  BarcodeScannerDialog,
  Icon,
  FlexBox,
  Text,
} from "@ui5/webcomponents-react";
// eslint-disable-next-line no-unused-vars
import "@ui5/webcomponents-icons/dist/AllIcons.js";
import { spacing } from "@ui5/webcomponents-react-base";
import React, { useState, useRef, createContext, useMemo } from "react";
import { ethers } from "ethers";
import "./App.css";
import { Route, HashRouter } from "react-router-dom";
import Asset from "./pages/Asset";
import QR from "./pages/QR";
import Error from "./pages/Error";
import Account from "./pages/Account";
import abi from "./abi.json";
import { isMobile } from "react-device-detect";
import WalletConnectProvider from "@walletconnect/web3-provider";
import { setTheme } from "@ui5/webcomponents-base/dist/config/Theme";
import { useTranslation } from "react-i18next";
import Feedback from "feeder-react-feedback"; // import Feedback component

import {
  FacebookShareButton,
  FacebookIcon,
  TwitterShareButton,
  TwitterIcon,
  WhatsappShareButton,
  WhatsappIcon,
  EmailShareButton,
  EmailIcon,
  LinkedinShareButton,
  LinkedinIcon,
} from "next-share";

export const rawdataContext = createContext({
  rawData: {
    openSeaId: undefined,
    ipfsId: undefined,
    polygonscanId: undefined,
  },
  setRawData: () => {},
});

let provider = null;
let signer = null;
// eslint-disable-next-line no-unused-vars
let address;
let roProvidernoUser = new ethers.providers.JsonRpcProvider(
  "https://polygon-mainnet.infura.io/v3/d8e7160dc78d48dba6ca40b11fdf1cd5" ||
    "https://rpc-mainnet.matic.network"
);
let tdao = new ethers.Contract(
  "0xe939789E151608D7442E2e6Ff4ea4E2eaf314cF3",
  abi,
  roProvidernoUser
);
const roProvider = new WalletConnectProvider({
  rpc: {
    137: "https://polygon-mainnet.infura.io/v3/d8e7160dc78d48dba6ca40b11fdf1cd5",
  },
});

export async function ethersConnect() {
  try {
    if (isMobile) {
      await roProvider.enable();
      provider = new ethers.providers.Web3Provider(roProvider);
    } else {
      provider = new ethers.providers.Web3Provider(window.ethereum, "any");
      await provider.send("eth_requestAccounts", []);
    }

    //[provider.getBlockNumber().then((b) => b - 10000),provider.getBlockNumber().then((b) => b )]

    signer = provider.getSigner();
    address = await signer.getAddress();
    tdao = new ethers.Contract(
      "0xe939789E151608D7442E2e6Ff4ea4E2eaf314cF3",
      abi,
      signer
    );

    //  let filter = tdao.filters.TransferSingle();
    // console.log(filter)
    // console.log(await tdao.filter.TransferSingle());
    // console.log(await tdao.queryFilter(filter, 17862525 , 17862925));

    return await signer.getAddress();
  } catch (error) {
    //alert("Please install Metamask!")
    console.log("e :" + error);

    return "Read only";
  }
}

export async function getTokenId(ipfsId) {
  try {
    ipfsId = ipfsId.split("/")[0];
    const rslt = tdao.getTokenId(ipfsId).then((result) => {
      return result.toNumber();
    });
    return rslt;
  } catch {
    return "";
  }
}

// function handleAccount(value) {
//   window.location.assign("/#/account/" + address);
// }

function handleHome() {
  window.location.assign("/#/");
}
setTheme("sap_horizon");

function App() {
  const [rawData, setRawData] = useState({
    polygonscanId:
      "https://polygonscan.com/address/0xe939789E151608D7442E2e6Ff4ea4E2eaf314cF3",
  });
  const context = useMemo(() => ({ rawData, setRawData }), [rawData]);
  const [value, setValue] = useState("");
  const [menu, setMenu] = useState("none");
  const [profile, setProfile] = useState(false);
  const { t } = useTranslation();

  const qrRef = useRef();

  const popoverRef = useRef();
  const popoverRefHelp = useRef();
  const onButtonClick = (e) => {
    popoverRef.current.showAt(e.target);
  };
  const onButtonClickHelp = (e) => {
    popoverRefHelp.current.showAt(e.target);
  };
  const handleClose = () => {
    //popoverRef.current.close();
    popoverRefHelp.current.close();
  };

  const [userAddress, setuserAddress] = useState(t("wallet"));

  return (
    <rawdataContext.Provider value={context}>
      <ThemeProvider>
        <HashRouter>
          <div id="shellbarDiv">
            <ShellBar
              id="shellbar"
              // primary-title="Home"
              logo={
                <img
                  alt="tdao"
                  mdxtype="img"
                  originaltype="img"
                  src="../tdao_cropped.svg"
                />
              }
              onLogoClick={() => handleHome()}
              style={{ overflow: "auto" }}
              className="Shell"
            >
              <StandardListItem
                data-key="1"
                icon="wallet"
                onSelectionChange={function noRefCheck() {}}
                onClick={() =>
                  ethersConnect().then((a) => {
                    setuserAddress(a.substring(0, 6) + "..." + a.slice(-4));
                    setProfile(!profile);
                  })
                }
                onChange={(e) => setuserAddress(e.target.userAddress)}
                text={userAddress}
                tooltip={userAddress}
              ></StandardListItem>
            </ShellBar>
          </div>
          <div id="content">
            <Route exact strict path="/">
              <div id="app" className="App">
                <div className="Scan">
                  <Avatar
                    colorScheme="Accent6"
                    size="XL"
                    icon="bar-code"
                    onClick={() => qrRef.current.show()}
                    tooltip="Click to Scan QR-Code"
                  />
                </div>
              </div>
            </Route>
            <Route path="/asset">
              <Asset />
            </Route>
            <Route path="/qr">
              <QR />
            </Route>
            <Route path="/error">
              <Error />
            </Route>
            <Route path="/account">
              <Account />
            </Route>
          </div>
        </HashRouter>
        <div id="sideBar">
          <Feedback projectId="620a6749b4e3e100046f50bc" />
          <Button
            id="helpBtn"
            icon="sys-help"
            onClick={onButtonClickHelp}
            design="Default"
          ></Button>
        </div>

        <div>
          <Popover
            className="help-wrapper-open"
            ref={popoverRefHelp}
            headerText="Information about our project"
            footer={
              <>
                <Button
                  design="Emphasized"
                  onClick={function () {
                    handleClose();
                  }}
                >
                  OK
                </Button>
              </>
            }
          >
            <Text style={{ ...spacing.sapUiResponsiveMargin }}>
              tDAO is a decentralized application that allows you to create and
              manage your own supply chain assets. If you want to learn more
              about the data constellation, check out the raw data now.
            </Text>
            <FlexBox
              style={{ width: "100%", marginTop: "10px", marginBottom: "5px" }}
            >
              <FlexBox
                direction="Column"
                style={{ flexGrow: 1 }}
                alignItems="Center"
              >
                <a
                  className={rawData.ipfsID === undefined ? "inactiveLink" : ""}
                  href={rawData.polygonscanId}
                  target="_blank"
                  rel="noreferrer"
                >
                  <Avatar size="S">
                    <img
                      src="../rawdata/polygonscn_icon.svg"
                      alt="Polygon Scan"
                    />
                  </Avatar>
                </a>
                <Text>{t("polygonscan")}</Text>
              </FlexBox>
              <FlexBox
                direction="Column"
                style={{ flexGrow: 1 }}
                alignItems="Center"
              >
                <a
                  className={rawData.ipfsID === undefined ? "inactiveLink" : ""}
                  href={`https://gateway.pinata.cloud/ipfs/${rawData.ipfsID}`}
                  target="_blank"
                  rel="noreferrer"
                >
                  <Avatar size="S">
                    <img src="../rawdata/ipfs_icon.svg" alt="IPFS" />
                  </Avatar>
                </a>
                <Text> {t("ipfs")} </Text>
              </FlexBox>
              <FlexBox
                direction="Column"
                style={{ flexGrow: 1 }}
                alignItems="Center"
              >
                <a
                  className={rawData.ipfsID === undefined ? "inactiveLink" : ""}
                  href={`https://opensea.io/assets/matic/0xe939789E151608D7442E2e6Ff4ea4E2eaf314cF3/${rawData.openSeaId}`}
                  target="_blank"
                  rel="noreferrer"
                >
                  <Avatar size="S">
                    <img src="../rawdata/opensea_icon.svg" alt="OpenSea" />
                  </Avatar>
                </a>
                <Text> {t("opensea")} </Text>
              </FlexBox>
            </FlexBox>
          </Popover>
        </div>
        <div>
          <BarcodeScannerDialog
            id="qrScan"
            ref={qrRef}
            onScanSuccess={(event) => {
              window.location.assign("/#/asset/" + event.detail.text);
              qrRef.current.close();
            }}
            onScanError={(event) => {
              console.log(event.detail.message);
              qrRef.current.close();
            }}
          />
        </div>
        <footer id="footer">
          <div id="footer-inner">
            <FlexBox className="flexContainer">
              <FlexBox
                direction="Column"
                style={{ flexGrow: 1 }}
                justifyContent="Center"
                alignItems="Start"
              >
                <Text className="poweredBy">
                  Made with <span style={{ color: "#e20074" }}>&#10084;</span>{" "}
                  by
                </Text>
                <img
                  id="tsystemslogo"
                  alt="T-Systems Logo"
                  src="../tsystemslogo.svg"
                />
              </FlexBox>
              <FlexBox
                direction="Column"
                style={{ flexGrow: 1 }}
                alignItems="End"
                justifyContent="End"
              >
                <div className="shareAndLaw">
                  <FacebookShareButton
                    url={"https://tdao.live/"}
                    quote={
                      "tDAO - Blockchain Technology for Supply Chain Tracking"
                    }
                    hashtag={"#blockchain"}
                  >
                    <FacebookIcon
                      className="shareButton"
                      size={32}
                      borderRadius="0.5rem"
                    />
                  </FacebookShareButton>
                  <TwitterShareButton
                    url={"https://tdao.live/"}
                    title={
                      "tDAO - Blockchain Technology for Supply Chain Tracking"
                    }
                  >
                    <TwitterIcon
                      className="shareButton"
                      size={32}
                      borderRadius="0.5rem"
                    />
                  </TwitterShareButton>
                  <WhatsappShareButton
                    url={"https://tdao.live/"}
                    title={
                      "tDAO - Blockchain Technology for Supply Chain Tracking"
                    }
                    separator=":: "
                  >
                    <WhatsappIcon
                      className="shareButton"
                      size={32}
                      borderRadius="0.5rem"
                    />
                  </WhatsappShareButton>
                  <LinkedinShareButton
                    className="shareButton"
                    url={"https://github.com/next-share"}
                  >
                    <LinkedinIcon
                      className="shareButton"
                      size={32}
                      borderRadius="0.5rem"
                    />
                  </LinkedinShareButton>
                  <EmailShareButton
                    className="shareButton"
                    url={"https://tdao.live/"}
                    subject={
                      "tDAO - Blockchain Technology for Supply Chain Tracking"
                    }
                    body="Feedback"
                  >
                    <EmailIcon
                      className="shareButton"
                      size={32}
                      borderRadius="0.5rem"
                    />
                  </EmailShareButton>
                </div>
              </FlexBox>
            </FlexBox>
            <FlexBox direction="row" alignItems="Center" justifyContent="End">
              <a
                href="https://www.t-systems.com/de/en/imprint"
                className="imprint"
              >
                {t("imprint")}
              </a>
              |
              <a
                href="https://www.t-systems.com/de/en/data-privacy"
                className="imprint"
              >
                {t("priv")}
              </a>
            </FlexBox>

            <br />
          </div>
        </footer>
      </ThemeProvider>
    </rawdataContext.Provider>
  );
}

export default App;
